import axios from 'axios';

export default {
  BASE_URL: 'https://f055fea9.ngrok.io/',

  get(uri, params, headers) {
    return axios.get(`${this.BASE_URL}${uri}`, {
      params,
      headers,
    });
  },

  post(uri, data, params, headers) {
    return axios.post(`${this.BASE_URL}${uri}`, data, {
      params,
      headers,
    });
  },

  put(uri, id, data, params, headers) {
    return axios.post(`${this.BASE_URL}${uri}/${id}`, data, {
      params,
      headers,
    });
  },
};
