import API from './API';

export default {
  BASE_URI: 'users/',

  get(id){
    const uri = id ? `${this.BASE_URI}${id}` : this.BASE_URI;
    return API.get(uri);
  },

  post(data){
      return API.post(this.BASE_URI, data);
  },

  put(id, data){ 
      return API.put(this.BASE_URI, id, data)
    },
};
