<template>
<section>
  <v-alert
      :value="error"
      color="error"
      icon="warning"
    >
      Failed to register
    </v-alert>
    <v-form
    ref="form"
    v-model="valid"
  >
  <v-img
                  :src="`/pics/logo.jpg`"
                  :lazy-src="`/pics/logo.jpg`"
                  aspect-ratio="1"
                  class="grey lighten-2"
                >
                  <template v-slot:placeholder>
                    <v-layout
                      fill-height
                      align-center
                      justify-center
                      ma-0
                    >
                      <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                    </v-layout>
                  </template>
                </v-img>
      <v-layout column>
      <v-flex xs12 md4>
        <v-text-field v-model="displayName" label="Name" required></v-text-field>
      </v-flex>
      <v-flex xs12 md4>
        <v-text-field v-model="age" label="Age" :counter=3 required></v-text-field>
      </v-flex>

      <v-flex xs12 md4>
        <v-overflow-btn
          :items="gendersList"
          label="Gender"
          v-model="gender"
          target="#dropdown-example"
        ></v-overflow-btn>
      </v-flex>
      <v-flex xs12 md4>
        <v-text-field v-model="company" label="Company" required></v-text-field>
      </v-flex>
      <v-flex xs12 md4>
        <v-text-field v-model="role" label="Role" required></v-text-field>
      </v-flex>

      <v-flex xs12 md4>
        <v-btn :disabled="!valid" small color="primary" @click="login">Register</v-btn>
      </v-flex>
    </v-layout>
    </v-form>
    
</section>
</template>

<script>
import users from '../services/users';

export default {
  data: () => ({
    displayName: '',
    age: null,
    gender: null,
    company: '',
    role: null,
    valid: false,
    error: false,
    gendersList: ["Male", "Female"]
  }),
  methods: {
    login:async function (){
      this.error = false;
      if(!this.valid){
        return;
      }
      try{
        const user = {
        displayName:this.displayName,
        age:this.age,
        gender: this.gender,
        company: this.company,
        role: this.role
      };
      await users.post(user);
      this.$store.dispatch('setUser',user);
      this.$router.push('menu');
      }catch(e){
        this.error = true;
        console.error(e);
      }

    }
  }
};
</script>

